.sh-burger-wrap {
  cursor: pointer;
  transition-duration: .3s; }
  .sh-burger-wrap:hover {
    opacity: .6; }
  @media screen and (min-width: 992px) {
    .sh-burger-wrap {
      display: none; } }
  @media screen and (max-width: 991px) {
    .sh-burger-wrap {
      width: 42px;
      position: relative;
      border: 1px solid #D1A954;
      border-radius: 50%; }
      .sh-burger-wrap::before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 100%; } }

.sh-burger {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -9px;
  margin-left: -13px;
  width: 26px;
  height: 15px;
  cursor: pointer;
  transition-duration: .2s; }
  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 26px;
    height: 1px;
    margin-left: -13px;
    background-color: #D1A954;
    border-radius: 10px; }
  .sh-burger span::before,
  .sh-burger span::after {
    content: '';
    transition: 0.2s; }
  .sh-burger span::before {
    transform: translateY(-7px); }
  .sh-burger span::after {
    transform: translateY(7px); }
  .sh-burger--active span {
    height: 0; }
    .sh-burger--active span::before {
      transform: rotate(45deg); }
    .sh-burger--active span::after {
      transform: rotate(-45deg); }

